.item--cart__delete-button {
    border: none;
    fill: none;
    background-image: url("delete-button__img.svg");
    background-size: cover;
    width: 30px;
    height: 35px;

    transition: background-image 0.2s;

    &:hover {
        background-image: url("delete-button__img_red.svg");
        cursor: pointer;
    }

    &:active {
        transform: scale(0.95);
      }
}
